/* 排版 */

.store__info {
  display: flex;
  justify-content: space-between;

  @include pad {
    flex-direction: column;
  }
}

.info__card {
  width: 42%;

  @include pad {
    width: 100%;

    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 2rem;
  }

  @include mobile {
    display: block;
    margin-bottom: 1rem;
  }
  &__img {
    padding-top: 50%;
    background-size: cover;
    background-position: center;

    @include pad {
      display: none;
      padding-top: 49%;
    }

    @include mobile {
      display: block;
      padding-top: 47%;
    }
  }

  &__body {
    @include pad {
      width: 48%;
    }
    @include mobile {
      width: 100%;
    }
  }

  &__title {
    border-right: 1px solid #00000020;
    border-left: 1px solid #00000020;

    padding: 12px 24px;

    @include pad {
      padding: 8px 16px;
      border-top: 1px solid #00000020;
    }
    @include mobile {
      padding: 8px 24px;
    }
  }
  &__content {
    border: 1px solid #00000020;

    padding: 16px 24px;

    li {
      i {
        width: 24px;
        height: 24px;
        text-align: center;

        color: $grayBlack;
      }

      p {
        display: inline;
      }

      a {
        color: $grayBlack;
      }
    }
  }
}

.pad__display {
  display: none;

  @include pad {
    display: block;
    width: 47%;
  }

  @include mobile {
    display: none;
  }
}

.map {
  width: 57%;
  height: auto;

  @include pad {
    width: 100%;
    height: 422px;
  }

  @include mobile {
    height: 211px;
  }
}
